function manualFacebookPostImport(message: string, date: string, picture: string, title?: string): FBPostResponse {
    return {
        created_time: date,
        full_picture: picture,
        id: 'id',
        is_hidden: false,
        is_published: true,
        title: title,
        message: message,
        from: {
            name: '',
            id: FB_PAGE_ID,
            error: ''
        },
        error: ''
    }
}